import React, { PropTypes } from 'react';
import { ClothesList } from './styledComponents';
import pricing from '../pricing';
import Cloth from './Cloth';
import assets from '../assets/assets';

const ClothesSelector = ({ orderItems, addItem }) => (
  <ClothesList>
    {pricing.map(cloth =>
      <Cloth
        key={cloth.slug}
        name={cloth.cloth}
        quantity={orderItems.reduce((total, item) => item.slug === cloth.slug ? total + 1 : total, 0)}
        icon={assets[cloth.slug]}
        onAdd={() => addItem(cloth.slug)}
      />,
    )}
  </ClothesList>
);

ClothesSelector.propTypes = {
  orderItems: PropTypes.arrayOf(PropTypes.shape({
    slug: PropTypes.string,
  })).isRequired,
  addItem: PropTypes.func.isRequired,
};

export default ClothesSelector;



